.peliData {
  width: 100%;
}

.Portada {
  width: 100%;
  display: flex;
  background-repeat: no-repeat;
  background-size: cover;
  height: 40vw;
  justify-content: center;
  text-shadow: 0px 0px 18px rgba(4, 1, 45, 0.66);
}
.PortadaImg {
  width: 30%;
  height: fit-content;
  border-radius: 22px;
  align-items: center;
  align-self: center;
}
.backgroundImg {
  position: absolute;
  z-index: -10;
  height: 40vw;
  max-height: 80%;
  width: 100%;
  filter: brightness(30%);
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
}
.imgPoster {
  border-radius: 12px;
  box-shadow: rgba(43, 43, 44, 0.445) 0px 7px 29px 0px;
  align-items: center;
  align-self: center;
  width: 75%;
  height: auto;
  opacity: 1;
  transition: all 0.5s;
}
.imgPoster:hover {
  transition: all 0.5s;
  scale: 1.05;
}
.PortadaInfo {
  display: flex;
  flex-direction: column;
  color: white;
  width: 60%;
  align-self: end;
  align-items: flex-end;
  align-content: end;
}
.PortadaTitle {
}
.Cast h3,
.Media h3 {
  font-size: 3vw;
  margin: 20px;
  text-align: center;
}
.titleMovie {
  font-size: 3vw;
}
.PortadaDatos {
}
.informacionPortada {
  font-size: 1.4vw;
  align-self: center;
  align-content: end;
  align-items: end;
}
.txtPelicula {
}
.infoPelicula {
  width: 100%;
  display: flex;
}
.informacion {
  width: 70%;
  margin-top: 0;
  padding-top: 0;
}
.CastSlider {
  display: flex;
  width: 100%;
}
.Slider {
  height: 320px;
}
.scrollmenu {
  height: auto;
  background-color: #333;
  overflow: auto;
  white-space: nowrap;
  margin: 10px 10px;
  box-shadow: 7px 7px 36px 0px rgba(0, 0, 0, 0.75);
}
.gameImg {
  border-radius: 10px;
  width: 98%;
  box-shadow: 10px 10px 56px 0px rgba(0, 0, 0, 0.75);
}
.sidebarCast {
  width: 25%;
}
.scrollmenu div {
  height: 300px;
  width: auto;
  display: inline-block;
  color: white;
  text-align: center;
  padding: 1px;
  text-decoration: none;
}
.video-responsive iframe {
  height: 300px;
  width: 400px;
}
.VistaGenera{
  padding: 1vw 4vw;
}
.descripcion{
  font-size: 2.3vw;
  text-align: justify;
}
.titleGeneral{
  font-size: 2.5vw;
  text-decoration:solid;
  
}
.badge{
  transition: all 0.5s ;
}
.badge:hover{
  transition: all 0.5s ;
  scale: 1.1;
}
@media screen and (max-width: 880px) {
  .infoPelicula {
    flex-direction: column-reverse;
  }

  .informacion {
    width: 100%;
  }
  .sidebarCast {
    width: 100%;
  }
}
@media screen and (max-width: 480px) {
  .Portada {
    flex-direction: column;
    height: 110vw;
  }
  .PortadaImg {
    width: 35%;
    height: fit-content;
    border-radius: 22px;
    align-items: center;
    align-self: center;
  }
  .backgroundImg {
    position: absolute;
    z-index: -10;
    height: 110vw;
    max-height: 80%;
    width: 100%;
    filter: brightness(30%);
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
  }
  .imgPoster {
    align-items: center;
    align-self: center;
    width: 100%;
    height: auto;
    opacity: 1;
    transition: all 0.5s;
  }
  .titleMovie {
    text-align: center;
    font-size: 5vw;
  }
  .PortadaInfo {
    flex-direction: column;
    color: white;
    width: 100%;
    padding: 4vw;
  }
  .informacionPortada {
    font-size: 2.4vw;
  }
  .Cast h3,
  .Media h3 {
    font-size: 6vw;
    margin: 20px;
    text-align: center;
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: #2b2d53 #374649;
}

*::-webkit-scrollbar {
  width: 10px;
}

*::-webkit-scrollbar-track {
  background: rgb(32, 32, 32);
}

*::-webkit-scrollbar-thumb {
  background-color: #14152b;
  border-radius: 10px;
  border: 2px outset #353131;
}

